/* Variables
/* ---------------------------------------------------------- */

:root {
    /* Colours */
    --color-primary: #139de7;
    --color-base: #3c484e;
    --color-bg-base: #f6f6f6;
    --color-bg-primary: #fefefe;

    /* Fonts */
    --font-sans-serif: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu,
        Cantarell, Open Sans, Helvetica Neue, sans-serif;
    --font-serif: Georgia, Times, serif;
    --font-mono: Menlo, Courier, monospace;
    --font-light: 100;
    --font-normal: 400;
    --font-bold: 700;
    --font-heavy: 800;

    /* Breakpoints */
    --xlarge: 1680px;
    --large: 1280px;
    --medium: 980px;
    --small: 740px;
    --xsmall: 480px;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;
}
